.galeria {
   padding: $separacion 0;

   h3 {
      text-align: center;
   }
}

.galeria-imagenes {
   padding: 0;
   list-style: none;
   margin-top: $separacion;
   @include grid(2, $separacion);
   @include tablet {
      @include grid(4, math.div($separacion, 2));
   }
   @include desktop {
      @include grid(6, math.div($separacion, 2));
   }
}

.overlay {
   position: fixed;
   background-color: rgba(0, 0, 0, 0.5);
   width: 100%;
   height: 100%;
   top: 0;
   display: grid;
   place-items: center;
}

.btn-cerrar {
   width: $separacion;
   height: $separacion;
   background-color: $blanco;
   display: grid;
   place-items: center;
   font-weight: $black;
   font-size: 3rem;
   border-radius: 50%;

   &:hover {
      cursor: pointer;
   }
}
